.error-wrap{
  display: flex;align-items: center;justify-content: center;height: 100vh;text-align: center;overflow: hidden;
  .error-icon{display: inline-block;background: url('../images/icon-error.svg') no-repeat 0 0;width: 70px;height: 70px;}
  strong{font-size: 18px;font-weight: 700;color: #000;line-height: 1.111;margin-top: 23px;display: block;}
  p{font-size: 14px;color: rgba(0,0,0,0.4);line-height: 1.428;margin-top: 16px;}
  .btn{width: 200px;margin-top: 40px;}
}

.login-menu{
  display: flex;justify-content: center;align-items: center;margin-top: 20px;
  a{font-size: 13px;color: $gray;}
}
.login-message{
  font-size: 14px;color: rgba(0,0,0,0.5);line-height: 1.714;margin-bottom: 20px;
}

.find-result{font-size: 28px;line-height: 1.78;}
.re-send{
  text-align: right;margin-top: 50px;margin-bottom: -10px;
  a{font-size: 13px;font-weight: 700;color: $muted;text-decoration: underline;}
}

.agree-area{
  margin-top: 40px;
  .form-check{margin-top: 15px;}
}

.lupick-collabo{
  padding-top: 50px;position: relative;padding-bottom: 340px;padding-left: 30px;
  &::before{content: '';display: block;width: 400px;height: 400px;border-radius: 50%;background: $primary; position: absolute;right: -200px;bottom: 0;z-index: -1;}
  .heading-text{
    font-size: 36px;font-weight: 700;display: block;margin-bottom: 30px;
    &::before{content: '';display: block;width: 57px;height: 57px;background: #000;border-radius: 50%;margin-bottom: 10px;}
  }
  .collabo-email{
    font-size: 16px;line-height: 1.75;margin-bottom: 30px;
    a{display: inline-block;border-bottom: 1px solid #000;padding-bottom:1px;}
  }
  .desc{font-size: 16px;}
}

.invitaion{
  img{max-width: 100%;}
  .id-copy{
    text-align: center;margin-top: 30px;margin-bottom: 95px;
    p{font-size: 14px;color: $gray;margin-bottom: 8px;}
    .my-id{
      border-bottom: 3px solid #000;color: $secondary;font-size: 32px;font-family: $font-family-eng;font-weight: 700; display: inline-block;padding-bottom: 2px;margin-bottom: 20px;
    }
  }
}

.lupickguide{
  padding-top: 30px;
  img{max-width: 100%;}
}

.terms{
  padding: 20px 0;font-size: 12px;color: #888;
  h2{margin-bottom: .75rem;margin-top: 2rem;color: #000;font-size: 14px;font-weight: 500;
      &:first-of-type{margin-top: 0;}
  }
  strong{display: block;margin-bottom: .5rem;margin-top: 1.25rem;color: #000;font-weight: 500;font-size: 13px;
      &:first-of-type{margin-top: 0;}
  }
  h2 + strong{margin-top: .75rem;}
  .table{
      margin: 10px 0;
      th,
      td{padding: 7px 8px;height: auto;}
      td{color: #666;}
      font-size: 11px;color: #666;
  }
}